import loaderm from '~/mixins/loader.js'
export default {
  mixins: [loaderm],
  props: {
    media: {
      type: Object,
      default: null,
    },
  },
  computed: {
    descripcion() {
      return this.media.descripcion
        ? this.$storyapi.richTextResolver.render(this.media.descripcion)
        : ''
    },
  },
}
